.x-actionsheet {
  align-items: flex-end !important;
}

.x-actionsheet .x-modal__inner {
  background: #fff;
  background: var(--x-panel-bg-color);
}

.x-actionsheet__title {
  width: 100%;
  padding: 0.2rem 0;
  min-height: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(235, 235, 238);
  border-bottom: 1px solid var(--x-border-color);
  font-size: 0.26rem;
}

.x-actionsheet__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(235, 235, 238);
  border-bottom: 1px solid var(--x-border-color);
  font-size: 0.3rem;
}

.x-actionsheet__item-con {
  width: 100%;
  padding: 0.2rem 0.4rem;
}

.x-actionsheet__button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1rem;
  width: 100%;
  color: rgb(60, 60, 90);
  color: var(--x-text-color);
  font-size: 0.3rem;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.x-actionsheet__button:active {
  background: rgba(0,0,0,0.05);
  opacity: 1;
}

.x-actionsheet__closebtn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(60, 60, 90, 0.5);
  color: var(--x-text-color-o50);
  width: 100%;
  height: 1rem;
  border-top: 1px solid rgb(235, 235, 238);
  border-top: 1px solid var(--x-border-color);
  font-size: 0.3rem;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.x-actionsheet__closebtn:active {
  background: rgba(0,0,0,0.05);
  opacity: 1;
}

.x-actionsheet.x-modal--enter .x-modal__bg {
  opacity: 1;
}

.x-actionsheet.x-modal--enter .x-modal__inner {
  -webkit-animation: __x_actionsheet_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_actionsheet_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

.x-actionsheet.x-modal--leave .x-modal__bg {
  opacity: 0;
}

.x-actionsheet.x-modal--leave .x-modal__inner {
  -webkit-animation: __x_actionsheet_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_actionsheet_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

@-webkit-keyframes __x_actionsheet_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes __x_actionsheet_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes __x_actionsheet_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}

@keyframes __x_actionsheet_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
