.x-spin {
  display: flex;
  align-items: center;
  justify-content: center;
}

.x-loading__spin {
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.34rem;
  width: 0.82rem;
}

.x-loading__spin span {
  width: 0.14rem;
  height: 0.14rem;
  display: block;
  border-radius: 50%;
  background: #00cf97;
  position: absolute;
  top: 0.1rem;
}

.x-loading__spin span:nth-child(1) {
  left: 0;
  -webkit-animation: __x_loading_bounce 0.8s linear infinite;
  animation: __x_loading_bounce 0.8s linear infinite;
}

.x-loading__spin span:nth-child(2) {
  left: 0.34rem;
  -webkit-animation: __x_loading_bounce 0.8s linear 0.2s infinite;
  animation: __x_loading_bounce 0.8s linear 0.2s infinite;
}

.x-loading__spin span:nth-child(3) {
  left: 0.68rem;
  -webkit-animation: __x_loading_bounce 0.8s linear 0.4s infinite;
  animation: __x_loading_bounce 0.8s linear 0.4s infinite;
}

@-webkit-keyframes __x_loading_bounce {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  50% {
    -webkit-transform: translateY(-0.1rem);
    transform: translateY(-0.1rem);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes __x_loading_bounce {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  50% {
    -webkit-transform: translateY(-0.1rem);
    transform: translateY(-0.1rem);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
