.x-dialog__inner {
  padding: 0.4rem;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.x-dialog__scroller {
  flex: 1 1;
  display: flex;
  overflow: hidden;
}

.x-dialog__inscroller {
  flex: 1 1;
  overflow: auto;
  margin: 0 -0.3rem 0 0;
  padding: 0 0.3rem 0 0;
  -webkit-overflow-scrolling: touch;
}

.x-dialog .x-modal__inner {
  display: block;
  background: #fff;
  background: var(--x-panel-bg-color);
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
  border-radius: 0.2rem;
  width: 85%;
  height: auto;
}

.x-dialog.x-modal--enter .x-modal__mask {
  opacity: 1;
}

.x-dialog.x-modal--enter .x-modal__inner {
  -webkit-animation: __x_dialog_enter 0.2s ease-out;
  animation: __x_dialog_enter 0.2s ease-out;
}

.x-dialog.x-modal--leave .x-modal__mask {
  opacity: 0;
}

.x-dialog.x-modal--leave .x-modal__inner {
  -webkit-animation: __x_dialog_leave 0.1s ease-out;
  animation: __x_dialog_leave 0.1s ease-out;
}

@-webkit-keyframes __x_dialog_enter {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes __x_dialog_enter {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

@-webkit-keyframes __x_dialog_leave {
  0% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 0;
  }
}

@keyframes __x_dialog_leave {
  0% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(0.95, 0.95);
