.x-switch {
  display: inline-flex;
  width: 1rem;
  height: 0.54rem;
  background-color: rgb(211, 211, 216);
  background-color: var(--x-switch-bg-color);
  border-radius: 0.27rem;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  position: relative;
}

.x-switch>em {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 0.46rem;
  height: 0.46rem;
  position: absolute;
  left: 0.04rem;
  top: 0.04rem;
  background-color: #fff;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.x-switch>em svg {
  width: 0.22rem;
  height: 0.22rem;
}

.x-switch>em path {
  fill: #ddd;
}

.x-switch.x-switch--active path {
  fill: rgb(0, 207, 151);
  fill: var(--x-primary-color);
}

.x-switch--active {
  background-color: rgb(0, 207, 151);
  background-color: var(--x-primary-color);
}

.x-switch--active em {
  -webkit-transform: translate(0.46rem, 0);
  transform: translate(0.46rem, 0);
}

.x-switch--disabled {
  opacity: 0.5;
}

.x-switch--disabled:active {
  opacity: 0.5 !important;
}

.x-switch--io {
  width: 1.2rem;
}

.x-switch--io sub,
.x-switch--io sup {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-style: normal;
  color: #fff;
  padding: 0 0.2rem;
  font-size: 0.24rem;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  opacity: 0;
}

.x-switch--io sub {
  justify-content: flex-end;
  opacity: 1;
}

.x-switch--io.x-switch--active sub {
  opacity: 0;
}

.x-switch--io.x-switch--active sup {
  opacity: 1;
}

.x-switch--io.x-switch--active em {
  -webkit-transform: translate(0.66rem, 0);
  transform: translate(0.66rem, 0);
}

.x-switch--mini {
  width: 0.78rem;
  height: 0.44rem;
}

.x-switch--mini>em {
  width: 0.36rem;
  height: 0.36rem;
}

.x-switch--mini.x-switch--active em {
  -webkit-transform: translate(0.34rem, 0);
  transform: translate(0.34rem, 0);
}
