.x-tabs {
  display: flex;
  height: 1rem;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.x-tabs--shrink .x-tabs__scroller {
  overflow: auto;
  height: 1.4rem;
  width: 100%;
  -webkit-overflow-scrolling: touch;
}

.x-tabs--shrink .x-tabs__inner {
  display: flex;
  height: 1rem;
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  overflow: hidden;
}

.x-tabs--shrink .x-tabs__item {
  width: auto;
  padding: 0 0.5rem;
  flex: none;
}

.x-tabs__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex: 1 1;
  margin: 0;
  position: relative;
}

.x-tabs__item>span {
  color: rgb(60, 60, 90);
  color: var(--x-text-color);
  font-size: 0.3rem;
}

.x-tabs__item:active {
  color: inherit;
}

.x-tabs__item::after {
  content: '';
  display: block;
  width: 0.63rem;
  height: 0.06rem;
  position: absolute;
  left: 50%;
  bottom: 0.1rem;
  margin-left: -0.3rem;
  border-radius: 0.03rem;
  opacity: 0;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  -webkit-transform-origin: 0;
  transform-origin: 0;
  background: -webkit-linear-gradient(right, #ffd100, rgba(255,209,0,0));
  background: linear-gradient(to left, #ffd100, rgba(255,209,0,0));
}

.x-tabs--active>span {
  color: rgb(0, 207, 151);
  color: var(--x-primary-color);
  font-weight: bold;
  -webkit-animation: __x_tabs_active_scale 0.2s ease-out;
  animation: __x_tabs_active_scale 0.2s ease-out;
}

.x-tabs--active>span:active {
  color: rgb(0, 207, 151);
  color: var(--x-primary-color);
  opacity: 1;
}

.x-tabs--active::after {
  opacity: 1;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

@-webkit-keyframes __x_tabs_active_scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes __x_tabs_active_scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
