:root {
  --x-primary-color: rgb(0, 207, 151);
  --x-primary-color-from: rgb(20, 215, 138);
  --x-primary-color-to: rgb(0, 207, 151);
  --x-danger-color: rgb(255, 89, 73);
  --x-danger-color-from: rgb(255, 89, 73);
  --x-danger-color-to: rgb(255, 77, 77);
  --x-default-button-bg-color: rgb(235, 235, 238);
  --x-default-button-border-color: rgb(211, 211, 216);
  --x-calendar-day-bg-color: #f2f2f2;
  --x-panel-bg-color: #fff;
  --x-switch-bg-color: rgb(211, 211, 216);
  --x-radio-bg-color: rgb(230, 230, 235);
  --x-border-color: rgb(235, 235, 238);
  --x-radio-border-color: rgba(0, 0, 0, 0.15);
  --x-input-border-color: #ebebee;
  --x-input-border-focus-color: rgb(196, 196, 205);
  --x-bg-color-lighter: #fff;
  --x-bg-color: #f2f2f2;
  --x-mask-color: rgba(0, 0, 0, 0.7);
  --x-loading-mask-color: rgba(255, 255, 255, 0.5);
  --x-cell-bg-color: #fff;
  --x-text-color: rgb(60, 60, 90);
  --x-text-color-o90: rgba(60, 60, 90, 0.9);
  --x-text-color-o80: rgba(60, 60, 90, 0.8);
  --x-text-color-o70: rgba(60, 60, 90, 0.7);
  --x-text-color-o60: rgba(60, 60, 90, 0.6);
  --x-text-color-o50: rgba(60, 60, 90, 0.5);
  --x-text-color-o40: rgba(60, 60, 90, 0.4);
  --x-text-color-o30: rgba(60, 60, 90, 0.3);
  --x-time-picker-mask-color-from: #fff;
  --x-time-picker-mask-color-to: rgba(255, 255, 255, 0.8);
  --x-time-picker-border-color: #f2f2f2;
}

@media (prefers-color-scheme: dark) {
  .x-app--support-dark-mode {
    --x-primary-color: rgb(0, 207, 151);
    --x-primary-color-from: rgb(20, 215, 138);
    --x-primary-color-to: rgb(0, 207, 151);
    --x-danger-color: rgb(255, 89, 73);
    --x-danger-color-from: rgb(255, 89, 73);
    --x-danger-color-to: rgb(255, 77, 77);
    --x-default-button-bg-color: rgba(255, 255, 255, 0.05);
    --x-default-button-border-color: rgba(255, 255, 255, 0.1);
    --x-calendar-day-bg-color: rgba(255, 255, 255, 0.05);
    --x-panel-bg-color: rgb(47, 47, 51);
    --x-switch-bg-color: rgba(255, 255, 255, 0.1);
    --x-border-color: #1e1f23;
    --x-radio-bg-color: rgba(255, 255, 255, 0.1);
    --x-radio-border-color: rgba(255, 255, 255, 0.2);
    --x-input-border-color: rgba(255, 255, 255, 0.2);
    --x-input-border-focus-color: rgb(0, 207, 151);
    --x-bg-color-lighter: #1f1f23;
    --x-bg-color: #18181c;
    --x-mask-color: rgba(0, 0, 0, 0.7);
    --x-loading-mask-color: rgba(0, 0, 0, 0.5);
    --x-cell-bg-color: #28282d;
    --x-text-color: rgb(211, 211, 211);
    --x-text-color-o90: rgba(211, 211, 211, 0.9);
    --x-text-color-o80: rgba(211, 211, 211, 0.8);
    --x-text-color-o70: rgba(211, 211, 211, 0.7);
    --x-text-color-o60: rgba(211, 211, 211, 0.6);
    --x-text-color-o50: rgba(211, 211, 211, 0.5);
    --x-text-color-o40: rgba(211, 211, 211, 0.4);
    --x-text-color-o30: rgba(211, 211, 211, 0.3);
    --x-time-picker-mask-color-from: rgba(47, 47, 51, 1);
    --x-time-picker-mask-color-to: rgba(47, 47, 51, 0.8);
    --x-time-picker-border-color: #18181c;
  }
}

.x-tabs {
  display: flex;
  height: 1rem;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.x-tabs--shrink .x-tabs__scroller {
  overflow: auto;
  height: 1.4rem;
  width: 100%;
  -webkit-overflow-scrolling: touch;
}

.x-tabs--shrink .x-tabs__inner {
  display: flex;
  height: 1rem;
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  overflow: hidden;
}

.x-tabs--shrink .x-tabs__item {
  width: auto;
  padding: 0 0.5rem;
  flex: none;
}

.x-tabs__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex: 1 1;
  margin: 0;
  position: relative;
}

.x-tabs__item>span {
  color: rgb(60, 60, 90);
  color: var(--x-text-color);
  font-size: 0.3rem;
}

.x-tabs__item:active {
  color: inherit;
}

.x-tabs__item::after {
  content: '';
  display: block;
  width: 0.63rem;
  height: 0.06rem;
  position: absolute;
  left: 50%;
  bottom: 0.1rem;
  margin-left: -0.3rem;
  border-radius: 0.03rem;
  opacity: 0;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  -webkit-transform-origin: 0;
  transform-origin: 0;
  background: -webkit-linear-gradient(right, #ffd100, rgba(255,209,0,0));
  background: linear-gradient(to left, #ffd100, rgba(255,209,0,0));
}

.x-tabs--active>span {
  color: rgb(0, 207, 151);
  color: var(--x-primary-color);
  font-weight: bold;
  -webkit-animation: __x_tabs_active_scale 0.2s ease-out;
  animation: __x_tabs_active_scale 0.2s ease-out;
}

.x-tabs--active>span:active {
  color: rgb(0, 207, 151);
  color: var(--x-primary-color);
  opacity: 1;
}

.x-tabs--active::after {
  opacity: 1;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

@-webkit-keyframes __x_tabs_active_scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes __x_tabs_active_scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}